import { CCard, CCardBody, CCardGroup, CCol, CContainer, CFormGroup, CRow, CInput, CButton, CLabel } from '@coreui/react';
import React, { Component } from 'react'
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import { HashLoader } from 'react-spinners';
import logo from '../../assets/logo.svg';
import axios from 'axios';
import {toast, ToastContainer} from 'react-toastify'
import 'react-bootstrap-table/dist/react-bootstrap-table-all.min.css';
import 'react-toastify/dist/ReactToastify.min.css'

class Converter extends Component {
    constructor(props){
        super(props);
        this.state = {
            isLoading: false,
            success: true,
            conversationId: '',
            accessKey: '',
            limit: 10,
            offset: 0,
            items: [],
            data: []
        }
    }

    handleSubmit = () => {
        const { accessKey, conversationId, limit, offset } = this.state
        var self = this;
        this.setState({
            isLoading: true,
            success: true
        })

        var _acc = 'AccessKey ' + accessKey

        if((accessKey === '' && conversationId === '') || (accessKey === null && conversationId === null)) {
            self.setState({
               isLoading: false,
               success: true
           }) 
            toast.error("Not found!")
        } else {
            axios({
                method: 'GET',
                url: 'https://conversations.messagebird.com/v1/conversations/' + conversationId + '/messages',
                headers: {
                    'Authorization': _acc,
                    // 'Access-Control-Allow-Origin': '*', 
                },
                data: {
                    limit: limit,
                    offset: offset
                }
            })
            .then((response) => {
                if(response.status === 200) {
                    console.log(response)
                    self.setState({
                        items: response.data.items
                    })
                    self.changeMessage()
                    self.setState({
                        isLoading: false,
                        success: true,
                        
                    })

                }
            })
            .catch(function(error){
                if(error.response) {
                    if(error.response.status === 401) {
                        self.setState({
                            isLoading: false,
                            success: false,
                        })
                        toast.error("Unathorized!")
                    }
                    else {
                        self.setState({
                            isLoading: false,
                            success: false,
                        })
                        toast.error("Something went wrong!")
                    }
                } else {
                    self.setState({
                        isLoading: false,
                        success: false,
                    })
                    toast.error("Something went wrong!")
                }
            })
        }
    }

    showDatatables = () => {
        const opt = {
            noDataText: 'No data yet...',
            defaultSortName: 'createdDatetime',
            defaultSortOrder: 'desc'
        }
        return (
            <BootstrapTable data={ this.state.data } exportCSV pagination search multiColumnSearch options={opt}>
                <TableHeaderColumn dataField='id' isKey={ true } dataSort>ID</TableHeaderColumn>
                <TableHeaderColumn dataField='channelId' searchable dataSort thStyle={{whiteSpace: 'normal'}} tdStyle={{whiteSpace: 'normal'}}>Channel ID</TableHeaderColumn>
                <TableHeaderColumn dataField='from' searchable dataSort thStyle={{whiteSpace: 'normal'}} tdStyle={{whiteSpace: 'normal'}}>From</TableHeaderColumn>
                <TableHeaderColumn dataField='to' searchable dataSort thStyle={{whiteSpace: 'normal'}} tdStyle={{whiteSpace: 'normal'}}>To</TableHeaderColumn>
                <TableHeaderColumn dataField='content' searchable dataSort thStyle={{whiteSpace: 'normal'}} tdStyle={{whiteSpace: 'normal'}}>Content</TableHeaderColumn>
                <TableHeaderColumn dataField='createdDatetime' dataSort thStyle={{whiteSpace: 'normal'}} tdStyle={{whiteSpace: 'normal'}}>Created On</TableHeaderColumn>
            </BootstrapTable>
        )
    }

    changeMessage = () => {
        const { items } = this.state
        var final = []

        for (var key in items){
            final[key] = {
            "id" : items[key]["id"],
            "from" : items[key]["from"],
            "to" : items[key]["to"],
            "channelId" : items[key]["channelId"],
            "createdDatetime" : items[key]["createdDatetime"],
            "updatedDatetime" : items[key]["updatedDatetime"],
            "content": items[key]["content"]["text"]
            };
        }

        this.setState({
            data: final
        })

        console.log(final)
    }

    showForms = () => {
        const { isLoading, success, conversationId, accessKey, offset, limit } = this.state;
        return (
            <div style={{'marginTop': '2em'}}>
                <ToastContainer
                    position="top-right"
                    autoClose={5000}
                    hideProgressBar={false}
                    newestOnTop={false}
                    closeOnClick
                    rtl={false}
                    pauseOnFocusLoss
                    draggable
                    pauseOnHover
                />
                <CFormGroup row>
                    <CCol md="4">
                        <CLabel style={{'fontSize': 'small'}}>Conv. ID</CLabel>
                    </CCol>
                    <CCol md="8">
                        <CInput type="text" disabled={isLoading} value={conversationId} onChange={(e) => this.setState({ conversationId: e.target.value })} />
                    </CCol>
                </CFormGroup>
                <CFormGroup row>
                    <CCol md="4">
                        <CLabel style={{'fontSize': 'small'}}>Access Key</CLabel>
                    </CCol>
                    <CCol md="8">
                        <CInput type="text" disabled={isLoading} value={accessKey} onChange={(e) => this.setState({ accessKey: e.target.value })} />
                    </CCol>
                </CFormGroup>
                <CFormGroup row>
                    <CCol md="4">
                        <CLabel style={{'fontSize': 'small'}}>Limit</CLabel>
                    </CCol>
                    <CCol md="8">
                        <CInput type="number" min="0" max="100" disabled={isLoading} value={limit} onChange={(e) => this.setState({ limit: e.target.value })} />
                    </CCol>
                </CFormGroup>
                <CFormGroup row>
                    <CCol md="4">
                        <CLabel style={{'fontSize': 'small'}}>Offset</CLabel>
                    </CCol>
                    <CCol md="8">
                        <CInput type="number" min="0" max="100" disabled={isLoading} value={offset} onChange={(e) => this.setState({ offset: e.target.value })} />
                    </CCol>
                </CFormGroup>
                <CFormGroup row>
                    <CCol md="12">
                        {
                            isLoading ? 
                            <center>
                                <HashLoader color={'#123abc'} loading={isLoading} /><br/> Please wait...
                            </center>
                            :
                            <center>
                                <CButton color="success" disabled={isLoading} onClick={() => this.handleSubmit()}>
                                    Convert
                                </CButton>
                            </center>
                        }
                    </CCol>
                </CFormGroup>
                <hr/>
                <h2>Result</h2>
                {
                    isLoading ? 
                    true 
                    :
                    (
                        success ?
                        this.showDatatables()
                        
                        :
                        true
                    )
                }
                <CRow>
                    
                </CRow>
            </div>
        )
    }

    render() {
        return (
            <>
                <div className="app flex-row align-items-center login-body">
                    <CContainer>
                        <CRow className="justify-content-center">
                            
                            <CCol md="8">
                                <CCardGroup>
                                    <CCard className="p-4">
                                        <CCardBody>
                                            <img src={logo} alt="mb-logo" className="mb-logo" style={{height: 'auto', width: '8em', marginBottom: '2em'}}/>
                                            <h2>Converter</h2>
                                            {this.showForms()}
                                        </CCardBody>
                                    </CCard>
                                </CCardGroup>
                            </CCol>
                        </CRow>
                    </CContainer>
                </div>
            </>
        )
    }
}

export default Converter;